/* eslint-disable react/forbid-prop-types */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Platform, StyleSheet, Text, View,
} from 'react-native';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import SettingsList from 'react-native-settings-list';
// import DateTimePicker from 'react-native-modal-datetime-picker';
// import FontAwesome from 'react-native-vector-icons/FontAwesome';
import moment from 'moment';
// import NumericInput from 'react-native-numeric-input';

import EnhancedPopup from '../components/EnhancedPopup';
import { DefaultHeaderStyle } from '../navigation/MainNavigator';
import {
  actionCreators,
  ALARM_TYPE_NONE,
  ALARM_TYPE_PLAY_BUILTIN_MUSIC,
  ALARM_TYPE_FAN,
  ALARM_TYPE_ITUNES_PLAYLIST,
} from '../store/Clock';

const styles = StyleSheet.create({
  settingsHeader: {
    paddingTop: 25,
  },
  fontAwesomeSettingsIcon: {
    textAlignVertical: 'center',
    color: 'green',
    fontSize: 20,
  },
  emptySettingsIcon: {
    width: 20,
  },
  modalView: {
    backgroundColor: 'white',
    margin: 0,
  },
});

class SleepTimeSettingsScreen extends Component {
  static propTypes = {
    setSleepAlarmType: PropTypes.func.isRequired,
    setManualSleepType: PropTypes.func.isRequired,
    startSettingSleepAlarmTime: PropTypes.func.isRequired,
    setSleepMusicTime: PropTypes.func.isRequired,
    isSettingSleepTime: PropTypes.bool,
    endSettingSleepAlarmTime: PropTypes.func.isRequired,
    cancelSetSleepAlarmTime: PropTypes.func.isRequired,
    sleepTimeState: PropTypes.any.isRequired,
  };

  static getDefaultProps = {
    isSettingSleepTime: false,
  };

  static navigationOptions = ({ navigation }) => ({
    ...DefaultHeaderStyle(navigation),
    title: 'Sleep Settings',
  })
  ;

  static formatAlarmTime(alarmTime) {
    const theTimeFormatted = moment();
    theTimeFormatted.hour(alarmTime.hour);
    theTimeFormatted.minute(alarmTime.minute);
    return theTimeFormatted.format('LT');
  }

  static getAlarmTimeAsDate(alarmTime) {
    const now = new Date();
    now.setHours(alarmTime.hour);
    now.setMinutes(alarmTime.minute);
    // ensure we also go forward in time
    if (now < new Date()) {
      now.setDate(now.getDate() + 1);
    }
    return now;
  }

  static formatPlayMusicFor(minutes) {
    if (!minutes) {
      return '15 minutes';
    }
    return `${minutes} minutes`;
  }

  constructor() {
    super();

    this.state = {
      playMusicForModalVisible: false,
      minimumHoursForMusic: 0,
      minimumMinutesForMusic: 15,
      isMinumimTimeForMusicDirty: false,
    };

    // this is needed because of the way the popup doesn't re-render on this state change
    // see https://stackoverflow.com/questions/37647061/how-do-i-access-refs-of-a-child-component-in-the-parent-component
    this.hoursNumericElement = React.createRef();
    this.minutesNumericElement = React.createRef();
  }

  componentWillReceiveProps(nextProps) {
    const { sleepTimeState } = nextProps;
    let { stopMusicAfterMinutes } = sleepTimeState;
    if (!stopMusicAfterMinutes) {
      stopMusicAfterMinutes = 15;
    }
    const minimumMinutesForMusic = stopMusicAfterMinutes % 60;
    const minumumHoursForMusic = (stopMusicAfterMinutes - minimumMinutesForMusic) / 60;
    const { isMinumimTimeForMusicDirty } = this.state;
    if (!isMinumimTimeForMusicDirty) {
      this.state = (prevState => ({
        ...prevState,
        minimumHoursForMusic: minumumHoursForMusic,
        minimumMinutesForMusic,
      }));
    }
  }

  onSleepTimeOptionDoNothing() {
    const { setSleepAlarmType } = this.props;
    setSleepAlarmType(ALARM_TYPE_NONE);
  }

  onSleepTimeOptionPlayMusic() {
    const { setSleepAlarmType } = this.props;
    setSleepAlarmType(ALARM_TYPE_PLAY_BUILTIN_MUSIC);
  }

  onSleepTimeOptionPlayFanNoise() {
    const { setSleepAlarmType } = this.props;
    setSleepAlarmType(ALARM_TYPE_FAN);
  }

  onManualSleepTimeOptionDoNothing() {
    const { setManualSleepType } = this.props;
    setManualSleepType(ALARM_TYPE_NONE);
  }

  onManualSleepTimeOptionPlayMusic() {
    const { setManualSleepType } = this.props;
    setManualSleepType(ALARM_TYPE_PLAY_BUILTIN_MUSIC);
  }

  onPlayMusicForClick() {
    this.setState(prevState => ({
      ...prevState,
      playMusicForModalVisible: true,
    }));
  }


  onMinimumHoursForMusicChanged(timeAmount) {
    this.setState(prevState => ({
      ...prevState,
      minimumHoursForMusic: timeAmount,
      isMinumimTimeForMusicDirty: true,
    }));
    this.hoursNumericElement.current.forceUpdate();
  }

  onMinimumMinutesForMusicChanged(timeAmount) {
    this.setState(prevState => ({
      ...prevState,
      minimumMinutesForMusic: timeAmount,
      isMinumimTimeForMusicDirty: true,
    }));
    this.minutesNumericElement.current.forceUpdate();
  }

  onSelectPlayMusicTimePopupCancelPressed() {
    const { sleepTimeState } = this.props;
    let { stopMusicAfterMinutes } = sleepTimeState;
    if (!stopMusicAfterMinutes) {
      stopMusicAfterMinutes = 15;
    }
    const minimumMinutesForMusic = stopMusicAfterMinutes % 60;
    const minumumHoursForMusic = (stopMusicAfterMinutes - minimumMinutesForMusic) / 60;
    this.state = (prevState => ({
      ...prevState,
      playMusicForModalVisible: false,
      isMinumimTimeForMusicDirty: false,
      minimumHoursForMusic: minumumHoursForMusic,
      minimumMinutesForMusic,
    }));
  }

  onSelectPlayMusicTimePopupConfirmPressed() {
    const { setSleepMusicTime } = this.props;
    const { minimumHoursForMusic, minimumMinutesForMusic } = this.state;
    setSleepMusicTime(minimumHoursForMusic * 60 + minimumMinutesForMusic);
    this.setState(prevState => ({
      ...prevState,
      playMusicForModalVisible: false,
      isMinumimTimeForMusicDirty: false,
    }));
  }

  getSelectedIcon(alarmType) {
    const { sleepTimeState } = this.props;
    if (alarmType === sleepTimeState.alarmAction) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  getManualSelectedIcon(alarmType) {
    const { sleepTimeState } = this.props;

    if (alarmType === sleepTimeState.manualTriggerAction) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  renderItunesIfIOS() {
    if (Platform.OS === 'ios') {
      const { navigation, sleepTimeState } = this.props;
      const playlistSong = sleepTimeState.alarmSubAction ? sleepTimeState.alarmSubAction : '';
      return (
        // <SettingsList.Item
        //   title="Play playlist"
        //   titleInfo={playlistSong}
        //   icon={this.getSelectedIcon(ALARM_TYPE_ITUNES_PLAYLIST)}
        //   hasNavArrow={false}
        //   onPress={() => navigation.navigate('SleepTimeItunesMusicSettings')}
        // />
        null
      );
    }
    return (null);
  }


  render() {
    const {
      sleepTimeState,
      startSettingSleepAlarmTime,
      isSettingSleepTime,
      endSettingSleepAlarmTime,
      cancelSetSleepAlarmTime,
    } = this.props;
    const { playMusicForModalVisible, minimumHoursForMusic, minimumMinutesForMusic } = this.state;
    return (
      <View>
        <EnhancedPopup
          visible={playMusicForModalVisible}
          showCancelButto
          showConfirmButton
          onCancelPressed={() => this.onSelectPlayMusicTimePopupCancelPressed()}
          onConfirmPressed={() => this.onSelectPlayMusicTimePopupConfirmPressed()}
        >
          <View style={styles.modalView}>
            <Text>
              Please select (the) amount of minimum amount of time
              music should play
            </Text>
            <View style={{ flexDirection: 'row', alignItems: 'center' }}>
              {/* <NumericInput
                ref={this.hoursNumericElement}
                type="up-down"
                minValue={0}
                maxValue={4}
                step={1}
                value={minimumHoursForMusic}
                onChange={value => this.onMinimumHoursForMusicChanged(value)}
              />
              <Text>Hours </Text>
              <NumericInput
                ref={this.minutesNumericElement}
                minValue={0}
                maxValue={59}
                step={1}
                value={minimumMinutesForMusic}
                onChange={value => this.onMinimumMinutesForMusicChanged(value)}
              />
              <Text>Minutes</Text> */}
            </View>
            <Text>Or select to play until manually stopped by tapping the screen:</Text>
            <Text>[Button goes here]</Text>
          </View>
        </EnhancedPopup>
        {/* <DateTimePicker
          isVisible={isSettingSleepTime}
          mode="time"
          datePickerModeAndroid="spinner"
          date={SleepTimeSettingsScreen.getAlarmTimeAsDate(sleepTimeState.alarmTime)}
          onConfirm={endSettingSleepAlarmTime}
          onCancel={cancelSetSleepAlarmTime}
        /> */}
      </View>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(SleepTimeSettingsScreen);
