/* eslint-disable react/forbid-prop-types */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Text, View, StyleSheet } from 'react-native';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import SettingsList from 'react-native-settings-list';
// import DateTimePicker from 'react-native-modal-datetime-picker';
// import FontAwesome from 'react-native-vector-icons/FontAwesome';
import moment from 'moment';

import { DefaultHeaderStyle } from '../navigation/MainNavigator';
import {
  actionCreators,
  ALARM_TYPE_NONE,
  ALARM_TYPE_PLAY_BUILTIN_MUSIC,
  ALARM_TYPE_FAN,
} from '../store/Clock';

const styles = StyleSheet.create({
  settingsHeader: {
    paddingTop: 25,
  },
  fontAwesomeSettingsIcon: {
    textAlignVertical: 'center',
    color: 'green',
    fontSize: 20,
  },
  emptySettingsIcon: {
    width: 20,
  },
});

class WakeTimeSettingsScreen extends Component {
  static propTypes = {
    setWakeAlarmType: PropTypes.func.isRequired,
    setManualWakeType: PropTypes.func.isRequired,
    startSettingWakeUpAlarmTime: PropTypes.func.isRequired,
    endSettingWakeUpAlarmTime: PropTypes.func.isRequired,
    cancelSetWakeUpAlarmTime: PropTypes.func.isRequired,
    isSettingWakeUpTime: PropTypes.bool,
    wakeTimeState: PropTypes.any.isRequired,
  };

  static getDefaultProps = {
    isSettingWakeUpTime: false,
  };

  static navigationOptions = ({ navigation }) => ({
    ...DefaultHeaderStyle(navigation),
    title: 'Wake Settings',
  });

  static formatAlarmTime(alarmTime) {
    const theTimeFormatted = moment();
    theTimeFormatted.hour(alarmTime.hour);
    theTimeFormatted.minute(alarmTime.minute);
    return theTimeFormatted.format('LT');
  }

  static getAlarmTimeAsDate(alarmTime) {
    const now = new Date();
    now.setHours(alarmTime.hour);
    now.setMinutes(alarmTime.minute);
    // ensure we also go forward in time
    if (now < new Date()) {
      now.setDate(now.getDate() + 1);
    }
    return now;
  }

  onWakeTimeOptionDoNothing() {
    const { setWakeAlarmType } = this.props;
    setWakeAlarmType(ALARM_TYPE_NONE);
  }

  onWakeTimeOptionPlayMusic() {
    const { setWakeAlarmType } = this.props;
    setWakeAlarmType(ALARM_TYPE_PLAY_BUILTIN_MUSIC);
  }

  onWakeTimeOptionPlayFanNoise() {
    const { setWakeAlarmType } = this.props;
    setWakeAlarmType(ALARM_TYPE_FAN);
  }

  onManualWakeTimeOptionDoNothing() {
    const { setManualWakeType } = this.props;
    setManualWakeType(ALARM_TYPE_NONE);
  }

  onManualWakeTimeOptionPlayMusic() {
    const { setManualWakeType } = this.props;
    setManualWakeType(ALARM_TYPE_PLAY_BUILTIN_MUSIC);
  }

  getSelectedIcon(alarmType) {
    const { wakeTimeState } = this.props;
    if (alarmType === wakeTimeState.alarmAction) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  getManualSelectedIcon(alarmType) {
    const { wakeTimeState } = this.props;
    if (alarmType === wakeTimeState.manualTriggerAction) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  render() {
    const {
      wakeTimeState,
      startSettingWakeUpAlarmTime,
      isSettingWakeUpTime,
      endSettingWakeUpAlarmTime,
      cancelSetWakeUpAlarmTime,
    } = this.props;
    return (
      <View>

        {/* <DateTimePicker
          isVisible={isSettingWakeUpTime}
          mode="time"
          datePickerModeAndroid="spinner"
          date={WakeTimeSettingsScreen.getAlarmTimeAsDate(wakeTimeState.alarmTime)}
          onConfirm={endSettingWakeUpAlarmTime}
          onCancel={cancelSetWakeUpAlarmTime}
        /> */}
      </View>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(WakeTimeSettingsScreen);
