import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
  Alert,
  Dimensions,
  StatusBar,
  StyleSheet,
  Text,
  TouchableWithoutFeedback,
  TouchableHighlight,
  View,
} from 'react-native';
import SoundWrapper from '../components/SoundWrapper';
// import HeaderButtons, { HeaderButton, Item } from 'react-navigation-header-buttons';
// import Sound from 'react-sound';
// import FontAwesome from 'react-native-vector-icons/FontAwesome';
// import KeepAwake from 'react-native-keep-awake';
// import MaterialIcons from 'react-native-vector-icons/MaterialIcons';

import Background from '../components/Background';
import Stars from '../components/Stars';
import { actionCreators, ALARM_TYPE_FAN, ALARM_TYPE_ITUNES_PLAYLIST } from '../store/Clock';
import { DefaultHeaderStyle } from '../navigation/MainNavigator';
import Player from '../services/Player';

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'column',
    backgroundColor: '#000',
    alignItems: 'center',
    justifyContent: 'center',
    width: '100%',
  },
  clockIcon: {
    color: '#999999',
    fontSize: 140,
  },
});

// const MaterialHeaderButtons = passMeFurther => (
//   // the `passMeFurther` variable here contains
//   // props from <Item .../> as well as <HeaderButtons ... />
//   // and it is important to pass those props to `HeaderButton`
//   // then you may add some information like icon size or color (if you use icons)
//   <HeaderButton {...passMeFurther} IconComponent={MaterialIcons} iconSize={32} color="#fff" />
// );

class Clock extends Component {
  static propTypes = {
    setScreenSize: PropTypes.func.isRequired,
    ensureNotFullScreen: PropTypes.func.isRequired,
    toggleFullScreen: PropTypes.func.isRequired,
    setFullscreenTipSeen: PropTypes.func.isRequired,
    toggleIsSleepTime: PropTypes.func.isRequired,
    time: PropTypes.string.isRequired,
    isSleepTime: PropTypes.bool.isRequired,
    sleepTimeState: PropTypes.any.isRequired,
    wakeTimeState: PropTypes.any.isRequired,
    shouldKeepAwake: PropTypes.bool,
    enteredFullScreenTipDoNotShowAgain: PropTypes.bool,
  };

  static getDefaultProps = {
    shouldKeepAwake: false,
    enteredFullScreenTipDoNotShowAgain: false,
  };

  static navigationOptions = ({ navigation }) => {
    const params = navigation.state.params || {};

    return {
      ...DefaultHeaderStyle(navigation),
      title: 'Sun and Stars',
      // headerLeft: (
      //   <HeaderButtons HeaderButtonComponent={MaterialHeaderButtons}>
      //     <Item
      //       title="Settings"
      //       iconName="settings"
      //       onPress={() => navigation.navigate('Settings')}
      //     />
      //   </HeaderButtons>
      // ),
      // headerRight: (
      //   <HeaderButtons HeaderButtonComponent={MaterialHeaderButtons}>
      //     <Item
      //       title="FullScreen"
      //       iconName="fullscreen"
      //       onPress={() => {
      //         params.toggleFullScreen();
      //       }}
      //     />
      //   </HeaderButtons>
      // ),
    };
  };

  constructor() {
    super();

    this.soundRef = React.createRef();

    // from https://shellmonger.com/2017/07/26/handling-orientation-changes-in-react-native/
    const { width, height } = Dimensions.get('window');
    this.state = {
      orientation: width > height ? 'landscape' : 'portrait',
      screenWidth: width,
      screenHeight: height,
    };
    Dimensions.addEventListener('change', () => {
      const newDimensions = Dimensions.get('window');
      const { setScreenSize } = this.props;
      this.setState(prevState => ({
        ...prevState,
        screenWidth: newDimensions.width,
        screenHeight: newDimensions.height,
        orientation: newDimensions.width > newDimensions.height ? 'landscape' : 'portrait',
      }));
      setScreenSize(width, height);
    });
  }

  componentDidMount() {
    const { setScreenSize, navigation } = this.props;
    const { screenWidth, screenHeight } = this.state;

    setScreenSize(screenWidth, screenHeight);

    if (navigation) {
      navigation.setParams({ toggleFullScreen: () => this.onToggleFullScreen() });
    }
  }

  onToggleFullScreen() {
    const {
      toggleFullScreen,
      enteredFullScreenTipDoNotShowAgain,
      setFullscreenTipSeen,
    } = this.props;
    toggleFullScreen();
    if (!enteredFullScreenTipDoNotShowAgain) {
      Alert.alert('Tip', 'Press and hold anywhere to leave full screen mode');
      setFullscreenTipSeen();
    }
  }

  clockStyle() {
    const { orientation } = this.state;
    const isLandscape = orientation === 'landscape';
    return {
      flex: 1,
      flexDirection: isLandscape ? 'row' : 'column',
      alignItems: 'center',
      justifyContent: 'center',
      width: '100%',
    };
  }

  timeTextStyle() {
    const { orientation, screenWidth } = this.state;

    const isLandscape = orientation === 'landscape';
    let fontSize = isLandscape ? 100 : 50;
    // special correction for smaller screens:
    if (isLandscape && screenWidth < 600) {
      fontSize = 70;
    }
    return {
      color: '#999999',
      fontSize,
      padding: 20,
      alignItems: 'center',
      justifyContent: 'center',
    };
  }

  singleTap() {
    const { isSleepTime, sleepTimeState, wakeTimeState } = this.props;
    const alarmAction = isSleepTime ? sleepTimeState.alarmAction : wakeTimeState.alarmAction;
    if (alarmAction === ALARM_TYPE_FAN) {
      Player.playFanOrPause({soundRef: this.soundRef});
    } else if (alarmAction === ALARM_TYPE_ITUNES_PLAYLIST) {
      const playlistName = isSleepTime
        ? sleepTimeState.alarmSubAction
        : wakeTimeState.alarmSubAction;
      Player.playITunesPlaylist(playlistName);
    } else {
      Player.playOrPause({soundRef: this.soundRef});
    }
  }

  renderKeepAwake() {
    const { shouldKeepAwake } = this.props;
    if (shouldKeepAwake) {
      // return <KeepAwake />;
    }
    return null;
  }

  renderClockIcon() {
    const { isSleepTime } = this.props;
    if (isSleepTime) {
      return <i class="fa fa-moon-o clockIcon"></i>;
      // return <FontAwesome name="moon-o" style={styles.clockIcon} />;
      return null;
    }

    
    // return <FontAwesome name="sun-o" style={styles.clockIcon} />;
    return <i class="fa fa-sun-o clockIcon" ></i>;
    return null;
  }

  render() {
    const { ensureNotFullScreen, toggleIsSleepTime, time } = this.props;
    return (
      <TouchableWithoutFeedback onLongPress={ensureNotFullScreen} onPress={() => this.singleTap()}>
        <View style={[styles.container, { height: 362 }]}>
          <StatusBar style={{ backgroundColor: 'transparent' }} hidden />
          <Background
            style={{
              position: 'absolute',
              left: 0,
              right: 0,
              top: 0,
              bottom: 0,
            }}
          />
          <Stars />
          <View style={this.clockStyle()}>
            <TouchableHighlight onPress={toggleIsSleepTime}>
              <View>
                {this.renderClockIcon()}
              </View>
            </TouchableHighlight>
            <Text style={this.timeTextStyle()} numberOfLines={1} onPress={() => this.singleTap()}>
              {time}
            </Text>
          </View>
          <SoundWrapper ref={this.soundRef} />
        </View>
      </TouchableWithoutFeedback>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(Clock);
