/* eslint-disable react/forbid-prop-types */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  StyleSheet, Text, View,
} from 'react-native';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import SettingsList from 'react-native-settings-list';
import moment from 'moment';
// import FontAwesome from 'react-native-vector-icons/FontAwesome';
import { actionCreators } from '../store/Clock';
import { DefaultHeaderStyle } from '../navigation/MainNavigator';
import EnhancedPopup from '../components/EnhancedPopup';

const styles = StyleSheet.create({
  fontAwesomeSettingsIcon: {
    textAlignVertical: 'center',
    color: 'green',
    fontSize: 20,
  },
  emptySettingsIcon: {
    width: 20,
  },
  modalView: {
    backgroundColor: 'white',
    margin: 0,
  },
  aboutText: {
    marginTop: 10,
  },
});

class Settings extends Component {
  static propTypes = {
    allowAnalytics: PropTypes.bool,
    setAnalyticsAllowed: PropTypes.func.isRequired,
    wakeTimeState: PropTypes.any.isRequired,
    sleepTimeState: PropTypes.any.isRequired,
    navigation: PropTypes.any.isRequired,
  };

  static getDefaultProps = {
    allowAnalytics: false,
  };

  static navigationOptions = ({ navigation }) => ({
    ...DefaultHeaderStyle(navigation),
    title: 'Settings',
  });

  static getAlarmTimeAsDate(alarmTime) {
    const now = new Date();
    now.setHours(alarmTime.hour);
    now.setMinutes(alarmTime.minute);
    // ensure we also go forward in time
    if (now < new Date()) {
      now.setDate(now.getDate() + 1);
    }
    return now;
  }

  static formatAlarmTime(alarmTime) {
    const theTimeFormatted = moment();
    theTimeFormatted.hour(alarmTime.hour);
    theTimeFormatted.minute(alarmTime.minute);
    return theTimeFormatted.format('LT');
  }

  constructor() {
    super();

    this.state = {
      isAboutModalVisible: false,
    };
  }

  onAboutPopupConfirmPressed() {
    this.setState(prevState => ({
      ...prevState,
      isAboutModalVisible: false,
    }));
  }

  getIsAnalyticsSelectedIcon() {
    const { allowAnalytics } = this.props;

    if (allowAnalytics) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  render() {
    const {
      wakeTimeState,
      sleepTimeState,
      navigation,
      setAnalyticsAllowed,
      allowAnalytics,
    } = this.props;
    const { isAboutModalVisible } = this.state;

    return (
      <View>
        <EnhancedPopup
          visible={isAboutModalVisible}
          onConfirmPressed={() => this.onAboutPopupConfirmPressed()}
        >
          <View style={styles.modalView}>
            <Text style={styles.aboutText}>Sun and Stars 1.0</Text>
            <Text style={styles.aboutText}>by Dan and Larissa Chenier</Text>
            <Text style={styles.aboutText}>Built in Music  Larissa Chenier</Text>
          </View>
        </EnhancedPopup>

        {/* <SettingsList>
          <SettingsList.Item
            title="Wake Time"
            titleInfo={Settings.formatAlarmTime(wakeTimeState.alarmTime)}
            onPress={() => navigation.navigate('WakeTimeSettings')}
          />
          <SettingsList.Item
            title="Sleep Time"
            titleInfo={Settings.formatAlarmTime(sleepTimeState.alarmTime)}
            onPress={() => navigation.navigate('SleepTimeSettings')}
          />
          <SettingsList.Item
            title="Send Usage"
            titleInfo="Send anonymous usage"
            onPress={() => setAnalyticsAllowed(!allowAnalytics)}
            icon={this.getIsAnalyticsSelectedIcon()}
          />
          <SettingsList.Item
            title="About"
            titleInfo="Sun and Stars 1.0"
            onPress={() => this.setState(prevState => ({
              ...prevState,
              isAboutModalVisible: true,
            }))
            }
          />
        </SettingsList> */}
      </View>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(Settings);
