import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Sound from 'react-sound';


export default class SoundWrapper extends Component {
  // static propTypes = {
  //   url: PropTypes.string,
  //   playStatus: PropTypes.any,
  //   onFinishedPlaying: PropTypes.func,
  // }

  // static getDefaultProps = {
  //   url: null,
  //   playStatus: Sound.status.STOPPED,
  //   onFinishedPlaying: null,
  // }

  constructor() {
    super();

    this.state = {
      url: null,
      playStatus: Sound.status.STOPPED,
      onFinishedPlaying: null,
    }
  }

  render() {
    const { url, playStatus, onFinishedPlaying } = this.state;

    return (
      <Sound url={url} playStatus={playStatus} onFinishedPlaying={onFinishedPlaying} />
    );  
  }
}