/* eslint-disable react/forbid-prop-types */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Alert, View, Text, StyleSheet,
} from 'react-native';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import SettingsList from 'react-native-settings-list';
// import FontAwesome from 'react-native-vector-icons/FontAwesome';
import { DefaultHeaderStyle } from '../navigation/MainNavigator';
import { actionCreators, ALARM_TYPE_ITUNES_PLAYLIST } from '../store/Clock';
import MusicService from '../services/MusicService';

const styles = StyleSheet.create({
  fontAwesomeSettingsIcon: {
    textAlignVertical: 'center',
    color: 'green',
    fontSize: 20,
  },
  emptySettingsIcon: {
    width: 20,
  },
});

class SleepTimeITunesSelection extends Component {
  static propTypes = {
    setSleepAlarmType: PropTypes.func.isRequired,
    sleepTimeState: PropTypes.any.isRequired,
  };

  static navigationOptions = ({ navigation }) => ({
    ...DefaultHeaderStyle(navigation),
    title: 'Music',
  });

  constructor() {
    super();
    this.state = { playlists: [] };
  }

  componentDidMount() {
    MusicService.getPlaylists().then((playlists) => {
      this.setState(prevState => ({
        ...prevState,
        playlists,
      }));
    }).catch((getPlaylistsError) => {
      Alert.Alert(`Unable to get playlists: ${getPlaylistsError}`);
    });
  }

  getSelectedIcon(playlistName) {
    const { sleepTimeState } = this.props;
    if (
      ALARM_TYPE_ITUNES_PLAYLIST === sleepTimeState.alarmAction
      && sleepTimeState.alarmSubAction === playlistName
    ) {
      // return <FontAwesome name="check" style={styles.fontAwesomeSettingsIcon} />;
      return null;
    }
    return <Text style={styles.emptySettingsIcon} />;
  }

  getPlaylistsAsSettings() {
    const { playlists } = this.state;
    const { setSleepAlarmType } = this.props;
    // a playlist has a name, playCount and tracks (tracks are of type TrackItem[])
    const items = playlists.map(playlist => (
      // <SettingsList.Item
      //   title={playlist.name}
      //   key="name"
      //   icon={this.getSelectedIcon(playlist.name)}
      //   hasNavArrow={false}
      //   onPress={() => {
      //     setSleepAlarmType(ALARM_TYPE_ITUNES_PLAYLIST, playlist.name);
      //   }}
      // />
      null
    ));
    return items;
  }

  render() {
    return (
      <View>
        {/* <SettingsList>{this.getPlaylistsAsSettings()}</SettingsList> */}
      </View>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(SleepTimeITunesSelection);
