// import Analytics from 'appcenter-analytics';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Text, View } from 'react-native';
import LinearGradient from 'react-native-web-linear-gradient';
import TimerMixin from 'react-timer-mixin';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import DeviceBrightness from 'react-native-device-brightness';
import { actionCreators } from '../store/Clock';

const darkColorBegin = { topColor: '#00192B', bottomColor: '#00477A' };
const darkColorEnd = { topColor: '#000B10', bottomColor: '#000A17' };
const lightColorBegin = { topColor: '#CACC96', bottomColor: '#CCCBBB' };
const lightColorEnd = { topColor: '#FCFFBC', bottomColor: '#FFFEEA' };
// const animationTime = 15 * 60 * 1000; // in milliseconds
const animationStep = 100; // time between redrawing, in milliseconds
const sleepTimeBrightness = 0.05;


const hexToRgba = (hex) => {
  if (typeof hex !== 'string' && hex.length > 0) { // is an array
    const returnValue = [];
    for (let i = 0; i < hex.length; i += 1) {
      returnValue.push(hexToRgba(hex[i]));
    }
    return returnValue;
  }

  let c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('');
    if (c.length === 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = `0x${c.join('')}`;
    return {
      // eslint-disable-next-line no-bitwise
      r: (c >> 16) & 255, g: (c >> 8) & 255, b: c & 255, a: 1,
    };
  }
  // Analytics.trackEvent('Bad Hex', { Category: 'Background', Value: hex });
  throw new Error('Bad Hex');
};

const componentToHex = (c) => {
  const hex = c.toString(16);
  return hex.length === 1 ? `0${hex}` : hex;
};

const rgbToHex = (r, g, b) => `#${componentToHex(Math.ceil(r))}${componentToHex(Math.ceil(g))}${componentToHex(Math.ceil(b))}`;

export class AnimatableGradient extends Component {
  static propTypes = {
    isSleepTime: PropTypes.bool.isRequired,
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.node),
      PropTypes.node,
    ]),
    wakeTimePostTransitionTime: PropTypes.number,
    sleepTimePostTransitionTime: PropTypes.number,
  }

  static getDefaultProps = {
    children: [],
    wakeTimePostTransitionTime: 60 * 1000,
    sleepTimePostTransitionTime: 60 * 1000,
  }

  constructor(props) {
    super(props);

    // linearGrandient needs initial colors
    this.state = {
      backgroundColors: ['#00192B', '#00477A'],
    };
  }


  componentDidMount() {
    this.setBackgroundColors();
  }

  componentWillReceiveProps(nextProps) {
    const that = this;
    console.log(`Updated props in Background at ${new Date()}`, nextProps);
    if (that.props.isSleepTime !== nextProps.isSleepTime) {
      // first cancel settingBackgroundColors if currently doing so
      if (that.settingBackgroundColors) {
        that.settingBackgroundColors.cancel = true;
      }
      // now change the colors for the "begin" color
      const beginColors = nextProps.isSleepTime
        ? [darkColorBegin.topColor, darkColorBegin.bottomColor]
        : [lightColorBegin.topColor, lightColorBegin.bottomColor];
      // DeviceBrightness.getBrightnessLevel().then((luminous) => {
        const luminous = 1;
        const startBrightness = nextProps.isSleepTime
          ? 1 : sleepTimeBrightness;

        const now = new Date();
        const endAnimationTime = new Date(now.getTime() + 1000);
        const animationFunc = that.animateBackgroundGrandient(that.state.backgroundColors,
          beginColors, now, endAnimationTime, 50, luminous, startBrightness);
        that.settingBackgroundColors = animationFunc;
        animationFunc()
          .then((animationStatus) => {
            if (!animationStatus.cancelled) {
              const endColors = nextProps.isSleepTime
                ? [darkColorEnd.topColor, darkColorEnd.bottomColor]
                : [lightColorEnd.topColor, lightColorEnd.bottomColor];
              const endBrightness = nextProps.isSleepTime
                ? sleepTimeBrightness : 1;
              let postTransitionTime = nextProps.isSleepTime
                ? nextProps.sleepTimePostTransitionTime : nextProps.wakeTimePostTransitionTime;
              if (!postTransitionTime) {
                postTransitionTime = 60 * 1000;
              }
              const animationFunc2 = that.animateBackgroundGrandient(that.state.backgroundColors,
                endColors, now, new Date(new Date().getTime() + postTransitionTime), animationStep,
                startBrightness, endBrightness);
              that.settingBackgroundColors = animationFunc2;
              animationFunc2().then(() => {});
            }
          });
      // });
    }
  }

  componentWillUnmount() {
    if (this.settingBackgroundColors) {
      this.settingBackgroundColors.cancel = true;
    }
  }

  setBackgroundColors() {
    const that = this;

    let fromColors; let toColors; let enteredStateAt; let endBrightness; let postTransitionTime;
    if (that.props.isSleepTime) {
      fromColors = [darkColorBegin.topColor, darkColorBegin.bottomColor];
      toColors = [darkColorEnd.topColor, darkColorEnd.bottomColor];
      enteredStateAt = that.props.sleepTimeEnteredAt;
      endBrightness = sleepTimeBrightness;
      postTransitionTime = that.props.sleepTimePostTransitionTime;
    } else {
      fromColors = [lightColorBegin.topColor, lightColorBegin.bottomColor];
      toColors = [lightColorEnd.topColor, lightColorEnd.bottomColor];
      enteredStateAt = that.props.wakeTimeEnteredAt;
      endBrightness = 1;
      postTransitionTime = that.props.wakeTimePostTransitionTime;
    }
    if (enteredStateAt) {
      // fix for the way the persistence works
      if (typeof enteredStateAt === 'string') {
        enteredStateAt = new Date(enteredStateAt);
      } else if (typeof enteredStateAt.getTime !== 'function' && typeof enteredStateAt.toDate === 'function') {
        // this is an object of type moment.  We need to convert it to an actual date object
        enteredStateAt = enteredStateAt.toDate();
      }
    } else {
      // what to do in this case?
      enteredStateAt = new Date(1, 1, 1);
    }

    if (!postTransitionTime) {
      postTransitionTime = 60 * 1000;
    }

    // DeviceBrightness.getBrightnessLevel().then((luminous) => {
      const luminous = 1;
      that.settingBackgroundColors = that.animateBackgroundGrandient(
        fromColors,
        toColors,
        enteredStateAt,
        new Date(enteredStateAt.getTime() + postTransitionTime),
        animationStep,
        luminous,
        endBrightness,
      );
      that.settingBackgroundColors();
    // });
  }

  animateBackgroundGrandient(fromColors, toColors, startTime, endTime, stepSize,
    fromBrightness, toBrightness) {
    const fromBrightnessFormatted = fromBrightness || 1;
    const toBrightnessFormatted = toBrightness || 1;

    const that = this;
    const settingBackgroundColors1 = () => new Promise((resolve) => {
      const settingBackgroundColors2 = () => {
        if (settingBackgroundColors1.cancel === true) {
          resolve({ cancelled: true });
          return;
        }

        let currentColors; let currentBrightness; let percentDone;
        const now = new Date();
        if (now >= endTime) {
          // easy case :)
          currentColors = toColors;
          currentBrightness = toBrightnessFormatted;
          percentDone = 1;
        } else if (startTime > now) {
          // this shouldn't happen
          currentColors = fromColors;
          currentBrightness = fromBrightnessFormatted;
          percentDone = 0;
        } else {
          percentDone = (now - startTime) / (endTime - startTime);

          // note this is an array, top is first, bottom is second
          const fromColorsHex = hexToRgba(fromColors);
          const toColorsHex = hexToRgba(toColors);
          currentColors = [
            rgbToHex(
              (toColorsHex[0].r - fromColorsHex[0].r) * percentDone + fromColorsHex[0].r,
              (toColorsHex[0].g - fromColorsHex[0].g) * percentDone + fromColorsHex[0].g,
              (toColorsHex[0].b - fromColorsHex[0].b) * percentDone + fromColorsHex[0].b,
              (toColorsHex[0].a - fromColorsHex[0].a) * percentDone + fromColorsHex[0].a,
            ),
            rgbToHex(
              (toColorsHex[1].r - fromColorsHex[1].r) * percentDone + fromColorsHex[1].r,
              (toColorsHex[1].g - fromColorsHex[1].g) * percentDone + fromColorsHex[1].g,
              (toColorsHex[1].b - fromColorsHex[1].b) * percentDone + fromColorsHex[1].b,
              (toColorsHex[1].a - fromColorsHex[1].a) * percentDone + fromColorsHex[1].a,
            ),
          ];
          currentBrightness = (toBrightnessFormatted - fromBrightnessFormatted) * percentDone
            + fromBrightnessFormatted;
        }
        settingBackgroundColors1.percentDone = percentDone;

        that.setState({
          ...that.state,
          backgroundColors: currentColors,
        });

        if (currentBrightness && currentBrightness >= 0 && currentBrightness <= 1) {
          try {
            // DeviceBrightness.setBrightnessLevel(currentBrightness);
          } catch (setDeviceBrightnessError) {
            console.log('Unable to set device brightness', setDeviceBrightnessError);
          }
        }

        if (percentDone < 1 && that.settingBackgroundColors === settingBackgroundColors1) {
          // then we do it again!
          TimerMixin.setTimeout(() => { settingBackgroundColors2(); }, stepSize);
        } else {
          resolve({ cancelled: settingBackgroundColors1.cancel });
        }
      };
      settingBackgroundColors2();
    });
    return settingBackgroundColors1;
  }

  render() {
    const { backgroundColors } = this.state;
    const { children } = this.props;
    return (
      <LinearGradient
        colors={backgroundColors}
        style={{
          position: 'absolute',
          left: 0,
          right: 0,
          top: 0,
          bottom: 0,
        }}
      >
        <View>
          {children}
        </View>
      </LinearGradient>
    );
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(AnimatableGradient);
