// import Analytics from 'appcenter-analytics';
import { actionCreators } from '../store/Clock';

const tick = (store) => {
  store.dispatch(actionCreators.tick());
  const secondsToNextMinute = 60 - new Date().getSeconds();
  setTimeout(() => tick(store), secondsToNextMinute * 1000);
};

let initialized = false;


export default store => next => (action) => {
  next(action);
  if (!initialized) {
    initialized = true;
    // from inspection, this is the first action in our stack
    // so start ticking!
    tick(store);
    // Analytics.trackEvent('App Starting');
  }
};
