// import Analytics from 'appcenter-analytics';
import moment from 'moment';
import { TimeState } from './Alarm';

export const TICK = 'clock/TICK';
export const TOGGLE_SLEEP_TIME = 'clock/TOGGLE_SLEEP_TIME';
export const TOGGLE_FULL_SCREEN = 'clock/TOGGLE_FULL_SCREEN';
export const ENSURE_NOT_FULL_SCREEN = 'clock/ENSURE_NOT_FULL_SCREEN';
export const SET_FULL_SCREEN_TIP_SEEN = 'clock/SET_FULL_SCREEN_TIP_SEEN';
export const SET_SCREEN_SIZE = 'clock/SET_SCREEN_SIZE';
export const START_SETTING_WAKE_UP_ALARM_TIME = 'clock/alarm/START_SETTING_WAKE_UP_ALARM_TIME';
export const END_SETTING_WAKE_UP_ALARM_TIME = 'clock/alarm/END_SETTING_WAKE_UP_ALARM_TIME';
export const START_SETTING_SLEEP_ALARM_TIME = 'clock/alarm/START_SETTING_SLEEP_ALARM_TIME';
export const END_SETTING_SLEEP_ALARM_TIME = 'clock/alarm/END_SETTING_SLEEP_ALARM_TIME';
export const CANCEL_SET_WAKE_UP_ALARM_TIME = 'clock/alarm/CANCEL_SET_WAKE_UP_ALARM_TIME';
export const CANCEL_SET_SLEEP_ALARM_TIME = 'clock/alarm/CANCEL_SET_SLEEP_ALARM_TIME';
export const SET_WAKE_ALARM_TYPE = 'clock/alarm/SET_WAKE_ALARM_TYPE';
export const SET_SLEEP_ALARM_TYPE = 'clock/alarm/SET_SLEEP_ALARM_TYPE';
export const SET_MANUAL_WAKE_TYPE = 'clock/alarm/SET_MANUAL_WAKE_TYPE';
export const SET_MANUAL_SLEEP_TYPE = 'clock/alarm/SET_MANUAL_SLEEP_TYPE';
export const ADD_STAR = 'clock/stars/ADD_STAR';
export const CLEAR_STARS = 'clock/stars/CLEAR_STARS';
export const SET_ANALYTICS_ALLOWED = 'clock/SET_ANALYTICS_ALLOWED';
export const SET_WAKE_MUSIC_TIME = 'clock/SET_WAKE_MUSIC_TIME';
export const SET_SLEEP_MUSIC_TIME = 'clock/SET_SLEEP_MUSIC_TIME';

export const ALARM_TYPE_NONE = 'clock/alarm/NONE';
export const ALARM_TYPE_PLAY_BUILTIN_MUSIC = 'clock/alarm/PLAY_BUILTIN_MUSIC';
export const ALARM_TYPE_FAN = 'clock/alarm/PLAY_FAN_NOISE';
export const ALARM_TYPE_ITUNES_PLAYLIST = 'clock/alarm/PLAY_ITUNES_PLAYLIST';

const isSleepTimeFunc = (now, wakeTime, sleepTime) => {
  const wakeTimeFull = new Date(
    now.getFullYear(),
    now.getMonth(),
    now.getDate(),
    wakeTime.hour,
    wakeTime.minute,
    0,
  );
  const sleepTimeFull = new Date(
    now.getFullYear(),
    now.getMonth(),
    now.getDate(),
    sleepTime.hour,
    sleepTime.minute,
    0,
  );
  if (wakeTimeFull <= sleepTimeFull) {
    if (now < wakeTimeFull) {
      return true;
    }

    return now >= sleepTimeFull;
  }
  if (now < sleepTimeFull) {
    return false;
  }
  return now < wakeTimeFull;
};

const initialWakeTime = {
  hour: 12,
  minute: 0,
  daysOfWeek: [0, 1, 2, 3, 4, 5, 6],
};

const initialSleepTime = {
  hour: 12,
  minute: 1,
  daysOfWeek: [0, 1, 2, 3, 4, 5, 6],
};

const initialState = {
  wakeTimeState: new TimeState({
    alarmTime: initialWakeTime,
    alarmAction: ALARM_TYPE_PLAY_BUILTIN_MUSIC,
    manualTriggerAction: ALARM_TYPE_PLAY_BUILTIN_MUSIC,
    stopMusicAfterMinutes: 15,
  }),
  sleepTimeState: new TimeState({
    alarmTime: initialSleepTime,
    alarmAction: ALARM_TYPE_PLAY_BUILTIN_MUSIC,
    manualTriggerAction: ALARM_TYPE_PLAY_BUILTIN_MUSIC,
    stopMusicAfterMinutes: 15,
  }),
  time: moment().format('LT'),
  isSleepTime: isSleepTimeFunc(new Date(), initialWakeTime, initialSleepTime),
  isFullScreen: false,
  isSettingWakeUpTime: false,
  isSettingSleepTime: false,
  shouldKeepAwake: true,
  screenSize: [800, 600],
  wakeTimeEnteredAt: null,
  sleepTimeEnteredAt: null,
  wakeTimePostTransitionTime: 1000,
  sleepTimePostTransitionTime:  1000,
  stars: [],
  enteredFullScreenTipDoNotShowAgain: false,
  allowAnalytics: true,
};

export const actionCreators = {
  tick: () => ({ type: TICK }),
  toggleIsSleepTime: () => ({ type: TOGGLE_SLEEP_TIME }),
  toggleFullScreen: () => ({ type: TOGGLE_FULL_SCREEN }),
  ensureNotFullScreen: () => ({ type: ENSURE_NOT_FULL_SCREEN }),
  setFullscreenTipSeen: () => ({ type: SET_FULL_SCREEN_TIP_SEEN }),
  setScreenSize: (width, height) => ({
    type: SET_SCREEN_SIZE,
    size: { width, height },
  }),
  startSettingWakeUpAlarmTime: () => ({ type: START_SETTING_WAKE_UP_ALARM_TIME }),
  endSettingWakeUpAlarmTime: selectedTime => ({
    type: END_SETTING_WAKE_UP_ALARM_TIME,
    time: selectedTime,
  }),
  startSettingSleepAlarmTime: () => ({ type: START_SETTING_SLEEP_ALARM_TIME }),
  endSettingSleepAlarmTime: selectedTime => ({
    type: END_SETTING_SLEEP_ALARM_TIME,
    time: selectedTime,
  }),
  cancelSetWakeUpAlarmTime: () => ({ type: CANCEL_SET_WAKE_UP_ALARM_TIME }),
  cancelSetSleepAlarmTime: () => ({ type: CANCEL_SET_SLEEP_ALARM_TIME }),
  setWakeAlarmType: (newType, subType) => ({
    type: SET_WAKE_ALARM_TYPE,
    alarmType: newType,
    subType,
  }),
  setSleepAlarmType: (newType, subType) => ({
    type: SET_SLEEP_ALARM_TYPE,
    alarmType: newType,
    subType,
  }),
  setManualWakeType: newType => ({ type: SET_MANUAL_WAKE_TYPE, alarmType: newType }),
  setManualSleepType: newType => ({ type: SET_MANUAL_SLEEP_TYPE, alarmType: newType }),
  addStar: boundary => ({
    type: ADD_STAR,
    boundary,
  }),
  clearStars: () => ({ type: CLEAR_STARS }),
  setAnalyticsAllowed: isAllowed => ({ type: SET_ANALYTICS_ALLOWED, isAllowed }),
  setWakeMusicTime: time => ({ type: SET_WAKE_MUSIC_TIME, time }),
  setSleepMusicTime: time => ({ type: SET_SLEEP_MUSIC_TIME, time }),
};

export const reducer = (storeState, action) => {
  const state = storeState || initialState;

  // fix for previous versions
  if (typeof state.stars === 'undefined') {
    state.stars = [];
  }

  if (action.type === TICK) {
    const now = moment();
    let isSleepTime = isSleepTimeFunc(
      now.toDate(),
      state.wakeTimeState.alarmTime,
      state.sleepTimeState.alarmTime,
    );

    let { sleepTimeEnteredAt, wakeTimeEnteredAt, isSleepTimeOverriden } = state;

    if (isSleepTimeOverriden && typeof state.isSleepTime !== 'undefined') {
      if (isSleepTime === state.isSleepTime) {
        // if here than we just transitioned into the sleepTime that
        // we overrode. So, it's no longer an override.
        isSleepTimeOverriden = false;
      } else {
        // otherwise we'll use the override state of isSleepTime
        // eslint-disable-next-line prefer-destructuring
        isSleepTime = state.isSleepTime;
      }
    }

    if (state.isSleepTime !== isSleepTime && typeof state.isSleepTime !== 'undefined') {
      // if here we will be transitioning into or out of sleep
      if (isSleepTime) {
        sleepTimeEnteredAt = now;
        wakeTimeEnteredAt = null;
      } else {
        wakeTimeEnteredAt = now;
        sleepTimeEnteredAt = null;
      }
    }
    return {
      ...state,
      time: now.format('LT'),
      isSleepTime,
      isSleepTimeOverriden,
      wakeTimeEnteredAt,
      sleepTimeEnteredAt,
    };
  }
  if (action.type === TOGGLE_SLEEP_TIME) {
    const now = moment();
    const theoreticalSleepTime = isSleepTimeFunc(
      now.toDate(),
      state.wakeTimeState.alarmTime,
      state.sleepTimeState.alarmTime,
    );
    const actualIsSleepTime = !state.isSleepTime;

    let { sleepTimeEnteredAt, wakeTimeEnteredAt } = state;
    if (actualIsSleepTime) {
      sleepTimeEnteredAt = now;
      wakeTimeEnteredAt = null;
    } else {
      wakeTimeEnteredAt = now;
      sleepTimeEnteredAt = null;
    }
    // Analytics.trackEvent('Sleep time Toggled', {
    //   Category: 'User',
    //   IsSleepTime: actualIsSleepTime,
    // });
    return {
      ...state,
      isSleepTime: actualIsSleepTime,
      // note isSleepTimeOverriden is true only when isSleepTime is not what its "supposed" to be
      isSleepTimeOverriden: theoreticalSleepTime !== actualIsSleepTime,
      wakeTimeEnteredAt,
      sleepTimeEnteredAt,
    };
  }
  if (action.type === TOGGLE_FULL_SCREEN) {
    return {
      ...state,
      isFullScreen: !state.isFullScreen,
    };
  }
  if (action.type === ENSURE_NOT_FULL_SCREEN) {
    return {
      ...state,
      isFullScreen: false,
    };
  }
  if (action.type === SET_FULL_SCREEN_TIP_SEEN) {
    return {
      ...state,
      enteredFullScreenTipDoNotShowAgain: true,
    };
  }
  if (action.type === SET_SCREEN_SIZE) {
    return {
      ...state,
      screenSize: [action.size.width, action.size.height],
    };
  }
  if (action.type === START_SETTING_WAKE_UP_ALARM_TIME) {
    return {
      ...state,
      isSettingWakeUpTime: true,
    };
  }
  if (action.type === START_SETTING_SLEEP_ALARM_TIME) {
    return {
      ...state,
      isSettingSleepTime: true,
    };
  }
  if (action.type === END_SETTING_WAKE_UP_ALARM_TIME) {
    // Analytics.trackEvent('Wake Time Set', {
    //   Category: 'Alarm',
    //   Hour: action.time.getHours(),
    //   Minute: action.time.getMinutes(),
    // });
    return {
      ...state,
      wakeTimeState: {
        ...state.wakeTimeState,
        alarmTime: {
          ...state.wakeTimeState.alarmTime,
          hour: action.time.getHours(),
          minute: action.time.getMinutes(),
        },
      },
      isSettingWakeUpTime: false,
    };
  }
  if (action.type === END_SETTING_SLEEP_ALARM_TIME) {
    // Analytics.trackEvent('Sleep Time Set', {
    //   Category: 'Alarm',
    //   Hour: action.time.getHours(),
    //   Minute: action.time.getMinutes(),
    // });
    return {
      ...state,
      sleepTimeState: {
        ...state.sleepTimeState,
        alarmTime: {
          ...state.sleepTimeState.alarmTime,
          hour: action.time.getHours(),
          minute: action.time.getMinutes(),
        },
      },
      isSettingSleepTime: false,
    };
  }
  if (action.type === CANCEL_SET_WAKE_UP_ALARM_TIME) {
    return {
      ...state,
      isSettingWakeUpTime: false,
    };
  }
  if (action.type === CANCEL_SET_SLEEP_ALARM_TIME) {
    return {
      ...state,
      isSettingSleepTime: false,
    };
  }
  if (action.type === SET_WAKE_ALARM_TYPE) {
    // Analytics.trackEvent('Wake alarm type set', { Category: 'Alarm', Action: action.alarmType });
    return {
      ...state,
      wakeTimeState: {
        ...state.wakeTimeState,
        alarmAction: action.alarmType,
        alarmSubAction: action.subType,
      },
    };
  }
  if (action.type === SET_SLEEP_ALARM_TYPE) {
    // Analytics.trackEvent('Sleep alarm type set', { Category: 'Alarm', Action: action.alarmType });
    return {
      ...state,
      sleepTimeState: {
        ...state.sleepTimeState,
        alarmAction: action.alarmType,
        alarmSubAction: action.subType,
      },
    };
  }
  if (action.type === SET_MANUAL_WAKE_TYPE) {
    return {
      ...state,
      wakeTimeState: {
        ...state.wakeTimeState,
        manualTriggerAction: action.alarmType,
      },
    };
  }
  if (action.type === SET_MANUAL_SLEEP_TYPE) {
    return {
      ...state,
      sleepTimeState: {
        ...state.sleepTimeState,
        manualTriggerAction: action.alarmType,
      },
    };
  }
  if (action.type === ADD_STAR) {
    const position = {
      x: Math.floor(
        Math.random() * (action.boundary.right - action.boundary.left) + action.boundary.left,
      ),
      y: Math.floor(
        Math.random() * (action.boundary.bottom - action.boundary.top) + action.boundary.top,
      ),
    };
    // add a penalty for brightness the farther down a star is
    const brightness = Math.floor(Math.random() * 255);
    const starIndex = Math.floor(Math.random() * 65536);

    const stars = state.stars || [];
    return {
      ...state,
      stars: stars.concat([{ position, brightness, starIndex }]),
    };
  }
  if (action.type === CLEAR_STARS) {
    return {
      ...state,
      stars: [],
    };
  }
  if (action.type === SET_ANALYTICS_ALLOWED) {
    // Analytics.setEnabled(action.isAllowed)
    //   .then(() => { });
    // .catch((setAnlyticsEnabledError) => {
    //   alert('setting analyics error: ' + setAnlyticsEnabledError);
    // });
    return {
      ...state,
      allowAnalytics: action.isAllowed,
    };
  }
  if (action.type === SET_WAKE_MUSIC_TIME) {
    return {
      ...state,
      wakeTimeState: {
        ...state.wakeTimeState,
        stopMusicAfterMinutes: action.time,
      },
    };
  }
  if (action.type === SET_SLEEP_MUSIC_TIME) {
    return {
      ...state,
      sleepTimeState: {
        ...state.sleepTimeState,
        stopMusicAfterMinutes: action.time,
      },
    };
  }

  return state;
};
