const RootStack = createSwitchNavigator({
  Home: {
    screen: ClockScreen,
  },
  Settings: {
    screen: SettingsScreen,
  },
  WakeTimeSettings: {
    screen: WakeTimeSettings,
  },
  SleepTimeSettings: {
    screen: SleepTimeSettings,
  },
  SleepTimeItunesMusicSettings: {
    screen: SleepTimeITunesSelection,
  },
});

const AppContainer = createBrowserApp(RootStack);

class MainNavigator extends Component {
  static propTypes = {
    isFullScreen: PropTypes.bool,
  };

  static getDefaultProps = {
    isFullScreen: false,
  };

  renderStackOrFullscreenClock() {
    const { isFullScreen } = this.props;
    if (isFullScreen) {
      return <ClockScreen />;
    }
    return <AppContainer />;
  }

  render() {
    return <ClockScreen />;
  }
}

export default connect(
  state => state.clock,
  dispatch => bindActionCreators(actionCreators, dispatch),
)(MainNavigator);
