import Player from '../services/Player';
import { TICK, ALARM_TYPE_PLAY_BUILTIN_MUSIC, ALARM_TYPE_FAN } from '../store/Clock';

export default store => next => (action) => {
  next(action);
  if (action.type === TICK) {
    const now = new Date();
    const clockState = (store.getState() || {}).clock || {};

    const wakeAlarm = clockState.wakeTimeState.alarmTime;
    const sleepAlarm = clockState.sleepTimeState.alarmTime;

    if (wakeAlarm.hour === now.getHours() && wakeAlarm.minute === now.getMinutes()) {
      if (clockState.wakeTimeState.alarmAction === ALARM_TYPE_PLAY_BUILTIN_MUSIC) {
        Player.ensurePlaying();
      } else if (clockState.sleepTimeState.alarmAction === ALARM_TYPE_FAN) {
        Player.playFan();
      }
    } else if (sleepAlarm.hour === now.getHours() && sleepAlarm.minute === now.getMinutes()) {
      if (clockState.sleepTimeState.alarmAction === ALARM_TYPE_PLAY_BUILTIN_MUSIC) {
        Player.ensurePlaying();
      } else if (clockState.sleepTimeState.alarmAction === ALARM_TYPE_FAN) {
        Player.playFan();
      }
    }
  }
};
