import React from 'react';
import {
  Dimensions,
  Modal,
  Platform,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';
import PropTypes from 'prop-types';
// import AndroidExtraDimensions from 'react-native-extra-dimensions-android';

const styles = StyleSheet.create({
  button: {
    padding: 12,
    margin: 16,
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: 4,
    borderColor: 'rgba(0, 0, 0, 0.1)',
  },
});

export default class EnhancedPopup extends React.PureComponent {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]),
    onCancelPressed: PropTypes.func,
    onConfirmPressed: PropTypes.func,
    showCancelButton: PropTypes.bool,
    showConfirmButton: PropTypes.bool,
  };

  static getDefaultProps = {
    children: [],
    showCancelButton: false,
    showConfirmButton: true,
    onCancelPressed: null,
    onConfirmPressed: null,
  };

  static modalViewWrapperStyle(deviceWidth) {
    // should check for platform here
    // and return different styles accordingly
    return {
      padding: 20,
      backgroundColor: 'white',
      width: deviceWidth - 40,
    };
  }

  static commandButtonsWrapperStyle(deviceWidth) {
    // should check for platform here
    // and return different styles accordingly
    return {
      padding: 20,
      backgroundColor: 'white',
      width: deviceWidth - 40,
    };
  }

  onCancelButtonPressed() {
    const { onCancelPressed } = this.props;
    if (onCancelPressed) {
      onCancelPressed();
    }
  }

  onConfirmButtonPressed() {
    const { onConfirmPressed } = this.props;
    if (onConfirmPressed) {
      onConfirmPressed();
    }
  }

  renderControlButtons() {
    const returnValue = [];
    let { showCancelButton, showConfirmButton } = this.props;
    if (typeof showCancelButton === 'undefined') {
      showCancelButton = false;
    }
    if (typeof showConfirmButton === 'undefined') {
      showConfirmButton = true;
    }
    if (Platform.OS === 'ios') {
      if (showCancelButton) {
        returnValue.push(
          <TouchableOpacity key="cancelButton" onPress={() => this.onConfirmButtonPressed()}>
            <View style={styles.button}>
              <Text>Cancel</Text>
            </View>
          </TouchableOpacity>,
        );
      }
      if (showConfirmButton) {
        returnValue.push(
          <TouchableOpacity key="confirmButton" onPress={() => this.onConfirmButtonPressed()}>
            <View style={styles.button}>
              <Text>OK</Text>
            </View>
          </TouchableOpacity>,
        );
      }
    } else {
      const androidButtonTextColor = '#0E9B8E';
      if (showCancelButton) {
        returnValue.push(
          <TouchableOpacity key="cancelButton" onPress={() => this.onConfirmButtonPressed()}>
            <View style={styles.button}>
              <Text style={{ color: androidButtonTextColor }}>Cancel</Text>
            </View>
          </TouchableOpacity>,
        );
      }
      if (showConfirmButton) {
        returnValue.push(
          <TouchableOpacity key="confirmButton" onPress={() => this.onConfirmButtonPressed()}>
            <View style={styles.button}>
              <Text style={{ color: androidButtonTextColor }}>OK</Text>
            </View>
          </TouchableOpacity>,
        );
      }
    }
    return returnValue;
  }

  render() {
    const { children, visible } = this.props;
    const deviceWidth = Dimensions.get('window').width;
    // const deviceHeight = Platform.OS === 'ios'
    //   ? Dimensions.get('window').height
    //   : AndroidExtraDimensions.get('REAL_WINDOW_HEIGHT');
    const deviceHeight = Dimensions.get('window').height;

    return (
      <Modal
        onRequestClose={() => {}}
        animationType="fade"
        transparent
        visible={visible}
        deviceWidth={deviceWidth}
        deviceHeight={deviceHeight}
        backdropOpacity={0.5}
      >
        <View
          style={{
            flex: 1,
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: 'rgba(0,0,0,0.5)',
          }}
        >
          <View
            style={EnhancedPopup.modalViewWrapperStyle(deviceWidth)}
          >
            {children}
          </View>
          <View style={EnhancedPopup.commandButtonsWrapperStyle(deviceWidth)}>
            {this.renderControlButtons()}
          </View>
        </View>
      </Modal>
    );
  }
}
