// import Analytics from 'appcenter-analytics';
import { actionCreators, TOGGLE_SLEEP_TIME, TICK } from '../store/Clock';

const numStars = 400;
// const initialWaitTime = 15 * 60 * 1000; // in milliseconds, past when we enter the state

let initialized = false;
let currentlyAddingStars = false;

// This is the (guassian) distribution of the rate stars are added.
// On average, the stars will be added with a rate of 500ms and a std dev of 200
// EDIT: Removed gaussian distribution because it didn't work well on IOS
const stepSizeDistribution = 20;

const addStarRecursive = (store, options) => {
  const clockState = (store.getState() || {}).clock || {};

  if (!clockState.isSleepTime) {
    // well we can just stop.
    currentlyAddingStars = false;
    return;
  }

  const theseOptions = options || {};
  const numToAdd = theseOptions.numStarsToAdd || 1;
  if (numToAdd < 1) {
    currentlyAddingStars = false;
    // Analytics.trackEvent('Invalid numStarsToAdd', { Category: 'StarMiddleware' });
    throw new Error('Invalid numStarsToAdd');
  }

  currentlyAddingStars = true;

  for (let i = 0; i < numToAdd; i += 1) {
    // const maxWidthHeight = 362; clockState.screenSize[0] > clockState.screenSize[1]
    //   ? clockState.screenSize[0]
    //   : clockState.screenSize[1];
    const maxHeight = 362/2;
    const maxWidth = 700/2;
    // const boundary = Math.floor(maxWidthHeight / 2);
    store.dispatch(
      actionCreators.addStar({
        top: 0,
        left: -(maxWidth),
        right: maxWidth,
        bottom: 2 * maxHeight,
      }),
    );
  }

  const allowRecursion = typeof theseOptions.allowRecursion === 'undefined' ? true : theseOptions.allowRecursion;
  if (allowRecursion) {
    const stars = clockState.stars || [];
    if (stars.length < numStars) {
      // we'll add another after a set time
      let randomStepSize = stepSizeDistribution; // .ppf(Math.random());
      if (randomStepSize < 10) {
        // ensure at least 10 milliseconds
        randomStepSize = 10;
      }
      setTimeout(() => addStarRecursive(store), randomStepSize);
    } else {
      currentlyAddingStars = false;
    }
  } else {
    currentlyAddingStars = false;
  }
};

export default store => next => (action) => {
  if (typeof store.getState !== 'function') {
    // easy case - this will not be a change to state.
    // this happens e.g. on application startup
    return next(action);
  }

  // const previousClockState = (store.getState() || {}).clock;
  const nextResult = next(action);

  if (action.type === 'persist/PERSIST') {
    // This is an early action before the state is recreated,
    // so the state is in initialState. We can ignore this.
    return nextResult;
  }

  const nextClockState = (store.getState() || {}).clock;

  // const previousIsSleepTime = (previousClockState || {}).isSleepTime;
  const nextIsSleepTime = (nextClockState || {}).isSleepTime;

  if (action.type === TOGGLE_SLEEP_TIME) {
    if (!nextIsSleepTime) {
      // we are transitioning to wake time. Get rid of the stars.
      currentlyAddingStars = false;
      store.dispatch(actionCreators.clearStars());
    } else {
      // force a store action in sleepTimePostTransitionTime
      // to make the stars appear
      setTimeout(() => {
        store.dispatch(actionCreators.tick());
      }, nextClockState.sleepTimePostTransitionTime + 100);
    }
  } 

  if (action.type === TICK) {
    if (nextIsSleepTime) {
      const currentNumberOfStars = nextClockState.stars.length;
      let initialWaitTime = nextClockState.sleepTimePostTransitionTime;
      if (!initialWaitTime) {
        initialWaitTime = 60 * 1000;
      }

      if (!initialized || currentNumberOfStars < numStars) {
        // we should start adding stars, but let's ensure we wait a certain
        // amount of time first, an "initialWaitTime"
        let { sleepTimeEnteredAt } = nextClockState;
        if (typeof sleepTimeEnteredAt === 'string') {
          sleepTimeEnteredAt = new Date(sleepTimeEnteredAt);
        }

        const millisPastEnteredAt = new Date() - sleepTimeEnteredAt;
        if (millisPastEnteredAt > initialWaitTime) {
          // then we need to start adding stars.
          if (!initialized) {
            initialized = true;
            // Analytics.trackEvent('Adding stars (1)', { Category: 'Debug' });

            // if we are here we are just initializing the app
            let numStarsShouldHaveAdded = Math.floor(
              (millisPastEnteredAt - initialWaitTime) / stepSizeDistribution,
            );
            if (numStarsShouldHaveAdded > numStars) {
              numStarsShouldHaveAdded = numStars;
            }
            addStarRecursive(store, {
              numStarsToAdd: numStarsShouldHaveAdded,
              allowRecursion: false,
            });
          } else if (!currentlyAddingStars) {
            // Analytics.trackEvent('Adding stars (2)', { Category: 'Debug' });
            addStarRecursive(store);
          } else {
            // Analytics.trackEvent('Not Adding stars (3)', { Category: 'Debug' });
          }
        }
      }
    } else {
      // ensure we don't have any stars
      const currentNumberOfStars = nextClockState.stars.length;
      if (currentNumberOfStars > 0) {
        store.dispatch(actionCreators.clearStars());
      }
    }
  }

  return nextResult;
};
