export const SET_ALARM_TIME = 'clock/alarm/SET_ALARM_TIME';
export const CANCEL_SET_ALARM_TIME = 'clock/alarm/CANCEL_SET_ALARM_TIME';

export const actionCreators = {
  setAlarmTime: () => ({ type: SET_ALARM_TIME }),
  cancelSetAlarmTime: () => ({ type: CANCEL_SET_ALARM_TIME }),
};

export function TimeState(initalTimeState) {
  return {
    alarmTime: initalTimeState.alarmTime,
    alarmAction: initalTimeState.alarmAction,
    manualTriggerAction: initalTimeState.manualTriggerAction,
  };
}
